import React from 'react';
import {Link, useNavigate} from "react-router-dom";
import {useDispatch} from "react-redux";
import store from "./member/Store";
import {logout} from "./member/Reducer";

function Menu() {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const state = store.getState();
    const handleLogout = () => {
        dispatch(logout());
        navigate("/main");
    };
    return(
        <div className="menubar">
            {state.isLoggedIn ? (
                <button className="login" onClick={handleLogout}></button>
            ) : (
                <button className="login" onClick={() => navigate("/member/login")}></button>
            )}

            <ul>
                <li onClick={() => navigate("/main")}></li>
                <li onClick={() => navigate("/animal")}>  </li>
                <li onClick={() => navigate("/recommend")}> </li>
                <li onClick={() => navigate("/board/list")}></li>
                <li onClick={() => navigate("/")}>  </li>
                <li className="dropdown">
                    <span> </span>
                    <div className="dropdown-content">
                        <Link to="/member/mypage">   </Link>
                        <Link to="/member/mypage"> </Link>
                        <Link to="/member/mypage">   </Link>
                    </div>
                </li>
            </ul>
        </div>
    );
}

export default Menu;